import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom'
import { HashRouter as Router, Route, Routes, Link, Outlet } from 'react-router-dom'
import axios from 'axios'
import ReactMarkdown from 'react-markdown'
import './App.css'

const Test = (props) => {
  const { index, data } = props.index

  console.log(index)

  const test12 = (a) => {
    if (data[index + a]) {
      return '/#/post/' + data[index + a].number
    }
    return '/#/'
  }

  return (
    <div className='right_box'>
      <ReactMarkdown>{props.body}</ReactMarkdown>
      <hr />
      <a href={test12(-1)}>上一页</a>
      <a href={test12(1)}>下一页</a>
      <br />
    </div>
  )
}

const App = (props) => {
  return (
    <>
      <div className='container'>
        <div className='left_box'>
          {props.data.map(item =>
            <Link key={item.id} to={'/post/' + item.number}>
              <p>{item.title}</p>
            </Link>
          )}
          <hr /><br />
        </div>
        <Outlet />
      </div>
    </>
  )
}

const Title = () => {
  const [data, setData] = useState([])

  useEffect(() => {
    axios.get('https://api.github.com/repos/rebase-network/who-is-hiring/issues?&per_page=300').then(res => {
      setData(res.data)
    })
  }, [])

  // console.log('test'+data)

  return (
    <Router>
      <Routes>
        <Route path='/' element={<App data={data} />}>
          {data.map((item, index) =>
            <Route key={item.id} path={'/post/' + item.number} element={<Test body={item.body} index={{ data, index }} />} />
          )}
        </Route>
      </Routes>
    </Router>
  )
}

const body = (
  <>
    <Title />
    {/* <Titlee /> */}
  </>
)

ReactDOM.render(body, document.querySelector('#root'))

